import React from "react"
import Layout from "../components/layout"
import { Link, graphql } from "gatsby"
import { FaUser, FaTag } from "react-icons/fa"
import BackgroundBlogImage from "../images/backgroundblog.jpg"
import "../styles/bootstrap.min.css"
import Head from "../components/Head"

export const query = graphql`
  query {
    allContentfulBlogPost(sort: { fields: publishedDate, order: DESC }) {
      edges {
        node {
          publishedDate(formatString: "MMMM Do, YYYY")
          title
          author
          description
          slug
          image {
            file {
              url
            }
          }
        }
      }
    }
  }
`

const Blog = props => {
  const data = props.data.allContentfulBlogPost
  return (
    <Layout>
      <Head title="Blog" />
      <section
        className="page-title"
        style={{ backgroundImage: `url(${BackgroundBlogImage})` }}
      >
        <div className="auto-container">
          <div className="text-center">
            <h1>Our Blog</h1>
          </div>
        </div>
      </section>
      <div className="sidebar-page">
        <div className="auto-container">
          <div className="row clearfix">
            {/* <!--Content Side-->	 */}
            {data.edges.map(edge => {
              return (
                <div
                  className="col-md-6 col-sm-6 col-lg-4"
                  key={edge.node.title}
                >
                  <section className="blog-container">
                    <div className="row clearfix">
                      {/* <!--Blog Post--> */}

                      <div
                        className="col-lg-12 col-md-12 col-sm-12 col-xs-12 featured-blog-post wow fadeInLeft"
                        data-wow-delay="0ms"
                        data-wow-duration="1500ms"
                      >
                        <article className="inner-box">
                          <figure className="image">
                            <img
                              src={edge.node.image.file.url}
                              alt={edge.node.image.title}
                            />
                          </figure>
                          <div className="post-lower">
                            <div className="post-header">
                              <div className="date">
                                {" "}
                                <span className="day">
                                  {edge.node.publishedDate}
                                </span>
                              </div>
                              <h3 className="title">{edge.node.title}</h3>
                              <ul className="post-info fs-13">
                                <li className="color-theme">
                                  <span>
                                    {" "}
                                    <FaUser />{" "}
                                  </span>
                                  {edge.node.author}
                                </li>
                                <li className="color-theme">
                                  <span>
                                    <FaTag />
                                  </span>
                                  Civil Rights
                                </li>
                              </ul>
                            </div>
                            <div className="post-desc">
                              <p>{edge.node.description}</p>
                              <div className="text-right">
                                <Link
                                  to={edge.node.slug}
                                  style={{
                                    boxShadow: `none`,
                                    textDecoration: `none`,
                                  }}
                                  className="color-theme fs-14"
                                >
                                  Read more
                                </Link>
                              </div>
                            </div>
                          </div>
                        </article>
                      </div>
                    </div>
                  </section>
                </div>
              )
            })}
          </div>
          {/* <!--Pagination--> */}
          {/* <div className="pager-outer">
            <ul className="pagination m-top0 m-btm30">
              <li>
                <Link to={``}>1</Link>
              </li>
              <li>
                <Link to={``}>2</Link>
              </li>
              <li className="active">
                <Link to={``}>3</Link>
              </li>
              <li>
                <Link to={``}>-</Link>
              </li>
              <li>
                <Link to={``}>7</Link>
              </li>
              <li>
                <Link to={``}>8</Link>
              </li>
              <li>
                <Link to={``}>Nex</Link>
              </li>
            </ul>
          </div> */}
        </div>
      </div>
    </Layout>
  )
}
export default Blog
